/* Arachne */
nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #333;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px -3px 10px rgba(0, 0, 0, 0.2); /* Add a shadow to the bottom */
}

nav button {
  width: 33%;
  background-color: #555;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

nav button:hover {
  background-color: #777;
}

.Submit_or_Cancel_container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

#modal {
  display: none; /* Hide modal by default */
  position: fixed; /* Position modal fixed to the viewport */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background overlay */
  z-index: 9999; /* Ensure modal appears above other content */
  overflow: auto; /* Enable scrolling if content exceeds viewport */
}

#modal.active {
  display: flex; /* Show modal when it has the 'active' class */
  justify-content: space-around;
  align-items: center;
}

.card.modal-card-for-form {
  & .card-header {
    display: flex;
    justify-content: space-around;
  }
  & .wrap-range-and-dates {
    display: flex;
    & .wrap-range-vertical {
      display: inline-flex;
      margin-right: 2rem;

      & div {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        & label {
          transform: translate(-40%) rotate(-90deg);
          transform-origin: center; /* Adjust origin as needed */
          writing-mode: horizontal-tb; /* Ensures proper rendering in modern browsers */
        }
      }

      & datalist {
        display: inline-flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        margin-top: -9%;
        margin-bottom: -4%;
        margin-left: -2.5rem;
        margin-right: -1%;
        & option {
          padding: 0;
        }
      }
      & input[type='range'] {
        writing-mode: vertical-lr;
      }
    }

    & .wrap-dates input {
      max-width: 13rem;
    }
  }
}
